$(() => {
  const ADDTHIS = {
    Init() {
      ADDTHIS.InitialiseAddThis();
    },
    InitialiseAddThis() {
      const addThisContainer = document.querySelectorAll('.js-add-this');

      if (addThisContainer && addThisContainer.length) {
        Array.prototype.forEach.call(addThisContainer, (el) => {
          const addThisSrc = $(el).data('add-this-src');
          if (addThisSrc) {
            const addthis_config = {
              "data_track_addressbar": false,
              "data_track_clickback": false,
              "services_compact": "facebook,twitter,email"
            };
            window['addthis_config'] = addthis_config;
            $.getScript(addThisSrc, function () {
              addthis.init();
            });
          }
        })
      }
    }
  }
  ADDTHIS.Init();
});
