import 'rangeslider.js';

$(() => {
  const SLIDER = {
    Init() {
      SLIDER.InitialiseSaverCalculator();
    },
    InitialiseSaverCalculator() {

      $('.slider input[type="range"]').rangeslider('destroy'); //clear previous instances
      $('.slider input[type="range"]').rangeslider({
        polyfill: false,
        onInit: function () {
          ; // pass
        }
      })
        .on('input', function () {
          ; // pass
        });
    }
  }
  // initalise if component on page
  if ($('.slider').length > 0) {
    SLIDER.Init();
  }
});
