const responsiveImage = () => {
  const imageSize = function () {
    // usage: apply the "responsive-image" class to your element as well as data-mobile-src and data-desktop-src to the same element
    // and this util will swap in the appropriate image depending on screen size as a background-image so it works with overlayed elements
    $('.responsive-img').each(function () {
      const $el = $(this);
      const mobileSrc = $el.data('mobile-src');
      const desktopSrc = $el.data('desktop-src');

      if (mobileSrc !== undefined && desktopSrc !== undefined)
        if ($(window).width() < 667 && mobileSrc !== '') {
          // if a mobile image has been supplied, let's use it, otherwise default to desktop image
          $el.css('background-image', "url(" + mobileSrc + ")");
        } else {
          $el.css('background-image', "url(" + desktopSrc + ")");
        }
    });

    // applied to images by default but only runs if the two data-attributes are present
    $('img').each(function () {
      const $imgEl = $(this);
      const mobileImgSrc = $imgEl.data('mobile-src');
      const desktopImgSrc = $imgEl.data('desktop-src');

      if (mobileImgSrc !== undefined || desktopImgSrc !== undefined)
        if ($(window).width() < 667 && mobileImgSrc !== '') {
          // if a mobile image has been supplied, let's use it, otherwise default to desktop image
          $imgEl.attr('src', mobileImgSrc);
        } else {
          $imgEl.attr('src', desktopImgSrc);
        }
    });
  }
  imageSize();
  $(window).smartresize(() => {
    imageSize()
  })
};

export default responsiveImage;
