$(() => {
  const ACCORDION = {
    Init() {
      ACCORDION.InitialiseAccordionItems();
    },
    InitialiseAccordionItems() {
      const accordionTrigger = $('.js-accordion-trigger');
      if (accordionTrigger) {
        $(accordionTrigger).on('click', function () {
          const accordionItem = $(this).closest('.accordion-item');
          const accordionBody = $(accordionItem).find('.accordion-item__body');
          const isOpen = $(accordionItem).hasClass('js-accordion-open');
          if (accordionBody) {
            if (isOpen) {
              $(accordionItem).removeClass('js-accordion-open');
            } else {
              $(accordionItem).addClass('js-accordion-open');
            }
          }
        });
      }
    },

  }
  // initalise if component on page
  if ($('.js-accordion-trigger').length > 0) {
    ACCORDION.Init();
  }
});
