import '@tarekraafat/autocomplete.js/dist/js/autoComplete.min.js';
import autoComplete from '@tarekraafat/autocomplete.js';


$(() => {
  const CENTRE_POSTCODE_LOCATOR = {
    InitMap() {
      //TODO ajax call to retrieve more info data for a centre
      let map; // GM map
      let locations = []; // location data
      let markers = []; // GM markers
      let infowindows = []; // GM infowindows
      let mapProp;
      // TODO generate infowindow content from search results
      const testInfoWindow = '<div id="content">' +
        '</div>' +
        '<h3 class="gm-infowindow-heading">Brinsmead Care Centre</h3>' +
        '<div id="bodyContent">' +
        '<p>999 High st, Brinsmead<br />Open: M-F 6:30am - 3pm, Sat 9am - 12pm</p>' +
        '<p> <a href="https://en.wikipedia.org/w/index.php?title=Uluru&oldid=297882194">' +
        'More info</a> ' +
        '</p>' +
        '</div>' +
        '</div>';

      let mapLat = $('.js-centre-postcode-locator-lat')[0].value; //FIX tidy this up
      let mapLon = $('.js-centre-postcode-locator-lon')[0].value; //FIX tidy this up
      const slider = document.querySelector('[class*="js-centre-postcode-locator-slider"]');
      const backbutton = document.querySelector('[class*="js-centre-postcode-locator-backbutton"]');

      var iconHighlight = new google.maps.MarkerImage(
        "http://maps.google.com/mapfiles/ms/icons/blue-dot.png",
        null, /* size is determined at runtime */
        null, /* origin is 0,0 */
        null, /* anchor is bottom center of the scaled image */
        new google.maps.Size(37, 37)
      );

      var iconDefault = new google.maps.MarkerImage(
        "http://maps.google.com/mapfiles/ms/icons/red-dot.png",
        null, /* size is determined at runtime */
        null, /* origin is 0,0 */
        null, /* anchor is bottom center of the scaled image */
        new google.maps.Size(30, 30)
      );

      /* * * * * *  FUNCTIONS * * * * * * * * */
      // toogle display of inforwindow for a map marker
      function toggleInfoWindow(infowindow, map, marker) {
        if (infowindow.getMap()) {
          infowindow.close();
        } else {
          openInfoWindow(infowindow, map, marker);
        }
      }

      function clearMarkers() {
        for (var i = 0; i < markers.length; i++) {
          markers[i].setMap(null);
        }
        markers = [];
      }

      // Set bounce animation on marker
      function toggleBounce(marker) {
        if (marker.getAnimation() !== null) {
          marker.setAnimation(null);
        } else {
          marker.setAnimation(google.maps.Animation.BOUNCE);
          setTimeout(marker.setAnimation(null), 2000);
        }
      }

      function openInfoWindow(infowindow, map, marker) {
        for (var i = 0; i < infowindows.length; i++) {
          infowindows[i].close(map, markers[i]);
          closeInfoWindow(infowindows[i], map, markers[i]);
        }
        infowindow.open(map, marker);
      }

      function closeInfoWindow(infowindow, map, marker) {
        infowindow.close(map, marker);
      }

      function createMarkers() {
        clearMarkers();
        for (var i = 0; i < locations.length; i++) {
          let marker = new google.maps.Marker({
            position: locations[i],
            icon: iconDefault,
            // map: map,
            animation: google.maps.Animation.DROP
          })
          // create info window
          let infowindow = new google.maps.InfoWindow({
            content: testInfoWindow
          });
          // add listener to pop info window
          marker.addListener('click', function () {
            toggleInfoWindow(infowindow, map, marker);
          });
          // Hover on search result changes marker colour
          searchResults[i].addEventListener('mouseover', function (e) {
            e.preventDefault();
            marker.setIcon(iconHighlight);
            openInfoWindow(infowindow, map, marker);
          });
          searchResults[i].addEventListener('mouseleave', function (e) {
            e.preventDefault();
            marker.setIcon(iconDefault);
            closeInfoWindow(infowindow, map, marker);
          });

          // Click on search result fires marker animation
          searchResults[i].addEventListener('click', function (e) {
            e.preventDefault();
            toggleBounce(marker);
          });

          // Open slider on button click
          searchResults[i].querySelector('[class*="js-centre-postcode-locator-find-out-more-button"]').addEventListener('click', function () {
            console.log("this fired");
            if (slider.classList.contains("slide-out")) {
              slider.classList.remove("slide-out");
              slider.classList.add('slide-in');
            }
          });

          // save elements
          markers.push(marker);
          infowindows.push(infowindow);
        }
      }

      // Add map markers with a drop animation
      function addLocationMarkersDrop() {
        //clearMarkers();
        for (var i = 0; i < markers.length; i++) {
          window.setTimeout(markers[i].setMap(map), i * 1000);
          //addMarkerWithTimeout(markers[i], i * 300);
          console.log(i * 1000);
        }
      }
      /* * * * * * * * *  * * * * * * * * */
      // render map
      mapProp = {
        center: new google.maps.LatLng(parseFloat(mapLat), parseFloat(mapLon)),
        zoom: 12
      };
      map = new google.maps.Map($(".js-centre-postcode-locator-map")[0], mapProp);
      let searchResults = document.getElementsByClassName('centre-postcode-locator__search-result');
      for (var n = 0; n < searchResults.length; n++) {
        locations[n] = {
          'lat': parseFloat(searchResults[n].getAttribute('data-lat')),
          'lng': parseFloat(searchResults[n].getAttribute('data-lon'))
        };
      }
      createMarkers();
      addLocationMarkersDrop();

      // Hide slider on click
      backbutton.addEventListener('click', function () {
        if (slider.classList.contains("slide-in")) {
          slider.classList.remove("slide-in");
          slider.classList.add('slide-out');
        }
      });
    },
    Init() {
      CENTRE_POSTCODE_LOCATOR.InitPostcodeLocator();
    },
    InitPostcodeLocator() {
      const centrePostcodeLocatorWrapper = document.querySelectorAll('.js-centre-postcode-locator');
      if (centrePostcodeLocatorWrapper && centrePostcodeLocatorWrapper.length > 0) {
        Array.prototype.forEach.call(centrePostcodeLocatorWrapper, (el) => {
          const formEl = el.querySelector('.js-centre-postcode-locator-form');
          const resultsEl = el.querySelector('.js-centre-search-results');

          if (formEl) {
            CENTRE_POSTCODE_LOCATOR.InitSubmitEvent(formEl, resultsEl);
            CENTRE_POSTCODE_LOCATOR.InitPostcodeTypeAheadEvent(formEl);
          }
        })
      }
    },
    InitPostcodeTypeAheadEvent(formEl) {
      const postcodeInput = formEl.querySelector('.js-postcode-typeahead');
      const latitudeHiddenField = $('.js-centre-postcode-locator-lat')[0];
      const longitudeHiddenField = $('.js-centre-postcode-locator-lon')[0];
      if (postcodeInput && $(postcodeInput).length > 0) {
        const locationLookupEndpoint = $(postcodeInput).data('location-lookup-endpoint');
        //initialise the autoComplete library https://tarekraafat.github.io/autoComplete.js/#/?id=_1-get-started
        const autocomplete = new autoComplete({
          data: { // Data src [Array, Function, Async] | (REQUIRED)
            src: async () => {
              // User search query
              let query = document.querySelector("#autoComplete").value;
              query = query.replace(/\s+/g, '');
              // Fetch External Data Source
              const source = await fetch(`${locationLookupEndpoint}?postcode=${query}`);
              // Format data into JSON
              const data = await source.json();
              // Return Fetched data
              return data;
            },
            key: ["Postcode"],
            cache: false
          },
          placeHolder: "Postcode",
          selector: "#autoComplete",
          threshold: 3,
          debounce: 300,
          searchEngine: "strict",
          resultsList: {
            render: true,
            container: source => {
              source.setAttribute("id", "locations");
            },
            destination: document.querySelector("#autoComplete"),
            position: "afterend",
            element: "ul"
          },
          maxResults: 5,
          highlight: true,
          resultItem: {
            content: (data, source) => {
              // console.log(data); //Debug only
              source.innerHTML = `${data.value.Postcode} - ${data.value.Suburb}`;
            },
            element: "li"
          },
          noResults: () => {
            const result = document.createElement("li");
            result.setAttribute("class", "no_result");
            result.setAttribute("tabindex", "1");
            result.innerHTML = "No Results";
            document.querySelector("#locations").appendChild(result);
          },
          onSelection: feedback => {
            latitudeHiddenField.value = feedback.selection.value.Latitude;
            longitudeHiddenField.value = feedback.selection.value.Longitude;
          }
        });
      }
    },
    InitSubmitEvent(formEl, resultsEl) {
      formEl.addEventListener("submit", function (e) {
        e.preventDefault();
        let query = $.objectifyForm($(this).serializeArray());
        const locatorEndpoint = $(formEl).data('locator-endpoint');
        const method = $(formEl).attr('method');
        if (query && query.OriginLatitude != '' && query.OriginLongitude != '') {
          // Ajax request
          $.ajax({
            url: locatorEndpoint,
            method: method,
            contentType: 'application/x-www-form-urlencoded; charset=utf-8',
            data: query,
            success: function (data) {
              if (data != null) {
                $(resultsEl).html(data);
                // populate map
                CENTRE_POSTCODE_LOCATOR.InitMap();
              } else {
                $('.js-centre-postcode-locator-map').html('<p>There were no results returned</p>');
              }
            }
          });
        }
      });
    }
  }
  // initalise if component on page
  CENTRE_POSTCODE_LOCATOR.Init();
});
